import React, { useState, useEffect } from 'react';
import { View, ScrollView, StyleSheet } from 'react-native';
import { NativeStackScreenProps } from '@react-navigation/native-stack';
import { useFoils } from '../hooks/useFoils';
import { useSearchFoils } from '../hooks/useSearchFoils';
import { useFilteredFoils } from '../hooks/useFilteredFoils';
import { useSortedFoils, SortField } from '../hooks/useSortedFoils';
import { useComparison } from '../hooks/useComparison';
import { SearchBar } from '../components/SearchBar';
import { FilterControls } from '../components/FilterControls';
import { FoilsTable } from '../components/FoilsTable';
import { ComparisonControls } from '../components/ComparisonControls';
import { ComparisonView } from '../components/ComparisonView';
import { LoadingIndicator } from '../components/LoadingIndicator';
import { ErrorMessage } from '../components/ErrorMessage';
import { Footer } from '../components/Footer';
import { FilterOptions } from '../types/filters';
import { RootStackParamList } from '../types/navigation';
import { BaseProduct } from '../types/scraper';

type Props = NativeStackScreenProps<RootStackParamList, 'Browse'>;

export default function BrowseScreen({ route, navigation }: Props) {
  const { foils, loading, error, refreshFoils } = useFoils();
  const searchQuery = route.params?.searchQuery || '';
  const { searchedFoils } = useSearchFoils(foils, searchQuery);
  const { 
    filters, 
    setFilters, 
    filteredFoils, 
    filterOptions 
  } = useFilteredFoils(searchedFoils);
  const { sortOption, setSortOption, sortedFoils } = useSortedFoils(filteredFoils);
  const { 
    comparisonMode, 
    selectedFoils, 
    setComparisonModeOn,
    setComparisonModeOff,
    toggleFoilSelection, 
    isSelected 
  } = useComparison();
  
  const [isComparisonView, setIsComparisonView] = useState(false);

  useEffect(() => {
    console.log('BrowseScreen mounted, loading:', loading);
  }, [loading]);

  useEffect(() => {
    if (route.params?.searchQuery !== searchQuery) {
      navigation.setParams({ searchQuery });
    }
  }, [searchQuery, navigation]);

  const handleFilterChange = (filterType: keyof FilterOptions, values: string[]) => {
    if (filterType === 'type') {
      setFilters((prev: FilterOptions) => ({
        ...prev,
        type: values.length > 0 ? values[0] as 'front' | 'tail' : null
      }));
    } else {
      setFilters((prev: FilterOptions) => ({
        ...prev,
        [filterType]: values
      }));
    }
  };

  const handleSort = (field: SortField) => {
    setSortOption(prev => ({
      field,
      direction: prev.field === field && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  const handleBrandFilter = (brand: string) => {
    setFilters((prev: FilterOptions) => ({
      ...prev,
      brands: prev.brands.includes(brand)
        ? prev.brands.filter((b: string) => b !== brand)
        : [...prev.brands, brand]
    }));
  };

  const handleSeriesFilter = (series: string) => {
    setFilters((prev: FilterOptions) => ({
      ...prev,
      series: prev.series.includes(series)
        ? prev.series.filter((s: string) => s !== series)
        : [...prev.series, series]
    }));
  };

  const handleCompareStart = () => {
    setComparisonModeOn();
  };

  const handleCompareSelected = () => {
    if (selectedFoils.length >= 2) {
      setIsComparisonView(true);
    }
  };

  const handleExitCompare = () => {
    setIsComparisonView(false);
    setComparisonModeOff();
  };

  if (loading) {
    console.log('Rendering LoadingIndicator');
    return <LoadingIndicator />;
  }

  if (error) {
    return <ErrorMessage message={error} onRetry={refreshFoils} />;
  }

  return (
    <View style={styles.container}>
      <ScrollView style={styles.scrollView}>
        <SearchBar 
          value={searchQuery} 
          onChangeText={(text) => navigation.setParams({ searchQuery: text })}
          onClear={() => navigation.setParams({ searchQuery: '' })}
        />
        <FilterControls
          filterOptions={filterOptions}
          selectedBrands={filters.brands}
          selectedSeries={filters.series}
          selectedTypes={filters.type ? [filters.type] : []}
          onFilterChange={handleFilterChange}
        />
        {isComparisonView ? (
          <ComparisonView 
            foils={selectedFoils} 
            onClose={() => setIsComparisonView(false)}
            onCompare={() => {}}
          />
        ) : (
          <FoilsTable
            foils={sortedFoils}
            onSort={handleSort}
            comparisonMode={comparisonMode}
            onFoilSelect={toggleFoilSelection}
            isSelected={isSelected}
            sortOption={sortOption}
          />
        )}
      </ScrollView>
      <Footer />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
  },
  scrollView: {
    flex: 1,
  },
}); 